.contacts {
    width: 100%;
    height: auto;
    background-color: rgb(241, 241, 241);
    padding: 140px 0px 119px 0px;
    &__title {
        color: #ff4522;
        font-weight: 500;
        display: inline-block;
        margin-bottom: 10px;
        font-size: 16px;
        line-height: 32px;
    }
    &__subtitle {
        font-weight: 800;
        text-transform: uppercase;
        font-size: 30px;
        line-height: 1.2;
    }
    &__wrapper {
        width: 100%;
        height: auto;
        display: flex;
        margin-top: 40px;
    }
    &__left {
        width: 50%;
        padding-right: 100px;
    }
    &__text {
        width: 100%;
        margin-bottom: 42px;
    }
    &__short {
        width: 100%;
    }
    &__right {
        width: 50%;
        padding-left: 100px;
    }
}